import Vue from 'vue';
import VueRouter from 'vue-router';
import Main from '../components/Main.vue';
import Band from '../components/Band.vue';
import Liam from '../components/Liam.vue';
import Noel from '../components/Noel.vue';
import Paul from '../components/Paul.vue';

Vue.use(VueRouter);

const routes = [
  { path: '/', component: Main },
  { path: '/band', component: Band },
  { path: '/liam', component: Liam },
  { path: '/noel', component: Noel },
  { path: '/paul', component: Paul },
];

export default new VueRouter({
    mode: 'history',
    routes
});