<template>
  <div class="main" >
    <div class="bio">
      <div class="bio-image-wrapper" v-lazy-container="{ selector: 'img' }">
        <img class="bio-image-wrapper__image" data-src="img/liam.png" alt="member image">
      </div>
      <div class="bio-text">
        <p>{{biography}}</p>
      </div>
    </div>
    <div class="social-wrapper">
      <a href="https://twitter.com/liamgallagher" class="social-link social-twitter">
        <font-awesome-icon :icon="['fab', 'twitter']" size="3x" />
      </a>
      <a href="https://www.instagram.com/liamgallagher/" class="social-link social-instagram">
        <font-awesome-icon :icon="['fab', 'instagram']" size="3x" />
      </a>
      <a href="https://open.spotify.com/artist/6sN51vEARnAAdBw1IKZ8Q9" class="social-link social-spotify">
        <font-awesome-icon :icon="['fab', 'spotify']" size="3x" />
      </a>
    </div>
    <div class="instagram-wrapper">
      <div class="instagram">
        <div v-for="column in columns" :key="column" class="col">
          <div v-for="item in column" :key="item" class="item-container">
            <a :href="item">
              <img :src="getInstagramImage(item)">
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      biography: `William John Paul \\"Liam\\" Gallagher (born 21 September 1972) is an English singer and songwriter. 
                    He rose to fame as the lead singer of the rock band Oasis, and later served as the singer of Beady Eye, 
                    before performing as a solo artist after the dissolution of both bands. 
                    His dress sense – with a penchant for Parka jackets – and abrasive manner have been the subject of commentary in the press; 
                    through these and his distinctive singing style – where he “twists vowels to the stretching point” – he remains one of the most recognisable figures in British music.`,
      instagram: [
          "https://www.instagram.com/p/B8KLmPEFnxN",
          "https://www.instagram.com/p/CAkWr02lvW-",
          "https://www.instagram.com/p/B7y2tV_FeUy",
          "https://www.instagram.com/p/B7i-4h9ltHi",
          "https://www.instagram.com/p/B7ROAooFbGD",
          "https://www.instagram.com/p/B7_TPy0l1-o"
      ]
    }
  },
  methods: {
    getInstagramImage: function(url) {
        return url + "/media?size=m";
    },
  },
  computed: {
    columns() {
      let columns = [];
      let cols = 3;
      let mid = Math.ceil(this.instagram.length / cols);
      for (let col = 0; col < cols; col++) {
          columns.push(this.instagram.slice(col * mid, col * mid + mid));
      }
      return columns;
    }
  }
}
</script>

<style scoped>
  .main span {
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-transform: capitalize;
  }

  .instagram-wrapper {
    display: flex;
    justify-content: center;
    padding-top: 50px;
  }

  .instagram {
    display: inline-flex;
    margin: auto;
  }

  .col {
    display: flex;
    flex-direction: column;
  }

  .item-container {
    height: 100%;
  }

  .item-container img {
    width: 250px;
    height: 250px;
    transition: all .2s ease-in-out;
  }

  .item-container img:hover {
    transform: scale(1.06);
  }

  .bio {
    display: flex;
  }

  .bio-image-wrapper {
    display: block;
  }

  .bio-image-wrapper__image {
    border-radius: 50%;
    max-width: 413px;
    max-height: 438px;
  }

  .bio-text {
    flex: 1;
    padding-left: 50px;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  .social-wrapper {
    display: flex;
    justify-content: center;
  }

  .social-link {
    margin: 0 15px;
    text-decoration: none;
    color: rgb(231, 231, 231);
    transition: color .2s ease-in;
  }

  .social-twitter:hover {
    color: rgb(29, 161, 242);
  }

  .social-instagram:hover {
    color: rgb(225, 48, 108);
  }

  .social-spotify:hover {
    color: rgb(30, 215, 96);
  }
</style>