<script>
export default {
  data() {
    return {
      biography: `Noel Thomas David Gallagher (born 29 May 1967) is an English singer, songwriter, and musician who holds both British and Irish citizenship.
                    He served as the primary lyricist, lead guitarist, and co-lead vocalist of the rock band Oasis. After leaving Oasis in 2009, he formed and became the lead vocalist, 
                    guitarist, and lyricist for Noel Gallagher's High Flying Birds. 
                    Gallagher began playing guitar at the age of 13, and became a roadie and technician for Inspiral Carpets at the age of 21.
                    He learned that his brother Liam had formed a band called The Rain, which eventually became Oasis, and Liam invited him to join them as the lead guitarist.
                    He agreed on condition that he would write their material. The band's debut album, Definitely Maybe (1994), was a critical and commercial success.
                    Their second album, (What's the Story) Morning Glory? (1995), reached the top of the album charts in many countries, and their third studio album,
                    Be Here Now (1997), became the fastest-selling album in UK chart history. Britpop eventually declined in popularity and Oasis failed to revive it,
                    though their final two albums, Don't Believe the Truth (2005) and Dig Out Your Soul (2008), were hailed as their best efforts in over a decade. In August 2009,
                    following an altercation with Liam, Noel announced his departure from Oasis.
                    Gallagher would go on to form Noel Gallagher's High Flying Birds, with whom he has released three studio albums.`,
      instagram: [
          "https://www.instagram.com/p/B-7hVmjjheu",
          "https://www.instagram.com/p/B8_rACoHBkM",
