<template>
  <div class="main">
    <div class="gallery">
      <button class="gallery-button" @click="slideLeft">
        <font-awesome-icon :icon="['fas', 'arrow-left']" size="3x" />
      </button>
      <transition name="fade" mode="out-in" appear>
        <img class="gallery-image" :src=gallery[currentImageIndex] :key="gallery[currentImageIndex]">
      </transition>
      <button class="gallery-button" @click="slideRight">
        <font-awesome-icon :icon="['fas', 'arrow-right']" size="3x" />
      </button>
    </div>
    <div class="video-wrapper">
      <iframe width="100%" height="600px" src="https://www.youtube.com/embed/3GCSUSwcDwg"></iframe>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      gallery: [
        'img/liam_noel_stage.jpg',
        'img/liam_stage.jpg'
      ],
      currentImageIndex: 0,
    }
  },
  methods: {
    slideLeft() {
      this.currentImageIndex > 0 ? this.currentImageIndex-- : this.currentImageIndex = this.gallery.length - 1;
    },
    slideRight() {
      this.currentImageIndex < this.gallery.length - 1 ? this.currentImageIndex++ : this.currentImageIndex = 0;
    }
  }
}
</script>

<style scoped>
  .fade-enter-active, .fade-leave-active {
    transition: opacity .3s ease;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
  }

  .main {
    margin: auto;
  }

  .gallery {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    background-color: rgba(7, 7, 7, 0.445);
    width: 100%;
  }

  .gallery-button {
    background-color: rgb(34, 34, 34);
    border: 0;
    width: 70px;
    outline: none;
    color: rgb(0, 0, 0);
    cursor: pointer;
    transition: color .4s ease-in-out, background-color .4s ease-in-out;
  }

  .gallery-button:hover {
    color: rgb(214, 214, 214);
    background-color: black;
  }

  .gallery-image {
    height: 600px;
  }

  .video-wrapper {
    padding-top: 40px;
  }

</style>