                  He went to St. Peter's Roman Catholic Grammar School in the nearby town of Prestwich. He is a cousin of actor Craig Cash. 
                  Arthurs left school in 1981 and worked as a plasterer. He started his first band, Pleasure and Pain, in 1984. 
                  Around this time, he began a relationship with his future wife Kate. In the late 1980s, while working as a building contractor, 
                  he started a band with his friends Paul "Guigsy" McGuigan, Tony McCarroll, and Chris Hutton. They called themselves The Rain, after the Beatles song "Rain".`,
        instagram: [
